html {
   height: 100%;
}

.game {
   margin-top: 40px;
   align-content: center;
   align-items:center;
   text-align: center;
   width:100%;
}

.game-card {
   background-color:#069AA8;
   box-sizing: content-box;
   margin:0 auto 1rem auto;
   border-radius:10px;
   width:20%;
   padding:1rem;
   position: relative;
   &__image-wrapper{
      padding:0.5rem;
      border-radius:5px;
   }

   &__image {
      width:100%;
      border-radius:5px;
   }

   &__question-wrapper {
      display:flex;
      margin-top:1rem;
      border-radius:5px;
      justify-content:center;
      vertical-align: center;
   }
   &__icon {
      width:20%;
      margin-right:1rem;
   }
   &__question {
      color:white;
      font-weight:700;
      font-size:20px;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   }
   
   &__answer-wrapper {
      margin-top:1rem;
      padding:0 0.5rem;
      display:flex;
      flex-wrap: wrap;
      justify-content: space-between;
   }

   &__answer {
      padding:0.5rem 0;
      background-color:#014D6D;
      border-radius:5px;
      color:white;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-weight:700;
      font-size: 15px;
      width:45%;
      &:hover{
         background-color: #ffc7c9;
         color:#014D6D;
         cursor: pointer;
      }
   }
   // removed button to go to next villager
   // &__next {
   //    padding:0.5rem 0;
   //    background-color: #ffc7c9;
   //    border: 0;
   //    border-radius:5px;
   //    color:#014D6D;
   //    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   //    font-weight:700;
   //    font-size: 15px;
   //    width:45%;
   //    &:hover{
   //       background-color: #014D6D;
   //       color:#ffc7c9;
   //    }
   // }

   &__point {
      position: absolute;
      bottom:20px;
      right:100px;
      background-color: #014D6D;
      padding:1rem;
      border-radius: 50px;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-weight:700;
      font-size: 30px;
      color:#ffc7c9;
   }

   &__life {
      position: absolute;
      bottom:20px;
      right:250px;
      background-color:#ffc7c9 ;
      padding:1rem;
      border-radius: 50px;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-weight:700;
      font-size: 30px;
      color:#014D6D;
   }

   &__correctAnswer {
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-weight: bold;
   }

   &.notPlaying {
      display: none;
   }

   &.gameOver {
      display: none;
   }

   &.gameOverStyle {
      margin-top: 200px;
      color: white;
   }

   &.gameOverButton {
      text-decoration: none;
      font-weight: bold;
      color: white;
   }
}

.hide__no-life {
   position: absolute;
   bottom:20px;
   right:250px;
   background-color:gray;
   padding:1rem;
   border-radius: 50px;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   font-weight:700;
   font-size: 30px;
   color:#ffc7c9;
   width:200px;
}

.timer {
   color: white;
}
